"use server";

type FormState = {
  message: string;
  errors?: {
    name?: string[];
    email?: string[];
    message?: string[];
  };
  success: boolean;
};

export async function submitContactForm(
  prevState: FormState,
  formData: FormData
): Promise<FormState> {
  // Contact form disabled
  return {
    message: "Contact form is currently disabled.",
    success: false,
    errors: {},
  };
}
